
.search_page{
    width: 100%;
    height: 100%;
    /* background-color: white; */
    overflow: auto;
}
.search_box{
    margin-top: 50px;
    width: 100%;
    height: 20px;
}
/* .search_space{
    width: 100%;
    height: 4%;
} */
.search_outer{
    margin:0 auto;
    width: 80%;
    border-radius: 6px;
    padding-left: 10px;
    background-color: rgb(33, 100, 201);
    display: flex;
    justify-content: space-between;
    padding: 5px;
}
.search_input{
    width: 90%;
    color: rgba(255, 255, 255, 0.7);
    background-color: rgb(33, 100, 201);
}
.search_icon{
    background-image: url("../../icons/search.png");
    background-size: 16px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    width: 10%;
}
