.search_fakebox{
    width: 100%;
    height: 5%;
    font-size: 16px;
}
.search_fake{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.search_fake_outer{
    width: 95%;
    /* border: solid 1px; */
    border-radius: 6px;
    padding-left: 10px;
    background-color: rgb(33, 100, 201);
    display: flex;
    justify-content: space-between;
    padding: 5px;
}
.search_fake_input{
    width: 90%;
    color: rgba(255, 255, 255, 0.7);
    background-color: rgb(33, 100, 201);

}
.search_fake_icon{
    background-image: url("../../icons/search.png");
    background-size: 16px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    width: 10%;
}
