.tagNav{
    width: 98%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    /* align-items:center; */
}
.tag {
    background-color: rgba(44, 59, 50, 0.2);
    color: rgba(255, 255, 255, 0.4);
    font-size: 15px;
    height: 20px;
    line-height: 20px;
    margin-top: 5px;
    text-align: center;
    border-radius: 3px;
}
.tagOn{
    background-color: rgba(44, 59, 50, 0.5);
    color: rgba(255, 255, 255, 0.7);
}

