.app{
    height: 100%;
    width: 100%;
    overflow: hidden;
    font-size: 16px;
    background: linear-gradient(180deg,#1948c4,#5d87eb 20%,#fff)
    /* #1948c4 */
    /* rgb(82, 129, 172) */
    /* rgb(136, 176, 205) */
    /* rgb(177, 212, 219); */
}
.appWraper{
    height: 100%;
    width: 95%;
    margin: 0 auto;
}

.nav{
    height: 5%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.link{
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 17px;
    border-radius: 5px;
    background-color: rgba(255,255,255,0.2);
    color: rgba(0, 0, 0, 0.5);
}
.linkActive{
    background-color: rgba(255,255,255,0.8);
    color: rgba(0, 0, 0, 0.9);
}

.searchCacheRoute{
    width: 100%;
    height: 100%;
    position: relative;
}